<template>
  <v-row class="py-6">
    <v-sheet
      class="mx-auto"
      elevation="8"
      max-width="100%"
    >
      <h1 class="px-6">Hour Above Alerts</h1>
      <v-slide-group
        v-model="model"
        class="pa-4"
        show-arrows
      >
        <v-slide-item
          v-for="(alert, j) in hourAboveAlertsFilter" :key="j"
        >
          <v-card
            class="ma-4"
          >
            <v-row
                class="fill-height"
                align="center"
                justify="center"
            >
              <v-card class="pa-0" @click="goToStockLookup(j)">
                <v-row justify="space-around">
                  <v-card-title>{{ j }}</v-card-title>
                  <v-card-title class="text-subtitle-1 text-center">
                    {{ alert.entry }}<br>{{ alert.price  | toCurrency()}}
                  </v-card-title>
                </v-row>
                <v-card-text class="secondary white--text">
                  <v-row justify="space-around" class="py-0">
                    <v-card-subtitle class="text-center">RSI:</v-card-subtitle>
                    <v-card-subtitle class="text-center">{{ alert.rsi }}</v-card-subtitle>
                  </v-row>
                  <v-row 
                    justify="space-around" 
                    class="py-0" 
                    :class="{ 'yellow--text' : alert.k == 0 && alert.d == 0}"
                  >
                    <v-card-subtitle class="text-center">stochK:</v-card-subtitle>
                    <v-card-subtitle class="text-center">{{ alert.k }}</v-card-subtitle>
                    <v-card-subtitle class="text-center">stochD:</v-card-subtitle>
                    <v-card-subtitle class="text-center">{{ alert.d }}</v-card-subtitle>
                  </v-row>
                </v-card-text>
              </v-card>
            </v-row>
          </v-card>
        </v-slide-item>
      </v-slide-group>
    </v-sheet>
  </v-row>
</template>

<script>
export default {
  name: 'aboveAlerts',
  data: () => ({
    model: null,
  }),
  components: {
  },
  mounted() {
  },
  methods: {
    goToStockLookup(stock){
      this.$router.push({ name: 'Stock', params: { id: stock } })
    },
    data_print() {
    },
  },
  filters: {
    toCurrency(amount) {
      var formatter = new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
      });
      return formatter.format(amount)
    },    
  },
  computed: {
    hourAboveAlertsFilter() {
      const above_list = {}
      for (const stock in this.$store.state.all_hour) {
        const stock_data = this.$store.state.all_hour[stock]
        if (stock_data.cloud_position === 'Above' && stock_data.entry === 'STRONG LONG ENTRY') {
          above_list[stock] = stock_data
        }
      }
      return above_list
    },
  }
}
</script>

