<template>
    <v-row>
        <v-col
            cols="12" 
        >
            <v-card class="pa-6">
                <v-card-title>Short</v-card-title>
                    <v-row no-gutters>
                    <v-col
                        v-for="(stock, i) in fifteenCrossFilter"
                        :key="i"
                        cols="3"
                        xs="2"
                        sm="2"
                        md="2"
                        lg="2"
                    >
                        <v-card
                            class="pa-2"
                            outlined
                            tile
                            @click="goToStockLookup(i)"
                            :class="{ 'blue white--text': stock.fifteen_stock_data.entry == 'STRONG SHORT ENTRY', 'red white--text': stock.fifteen_stock_data.entry == 'Strong Downtrend'}"                            
                        >
                            {{ i }}
                        </v-card>
                    </v-col>
                </v-row>          
            </v-card>
        </v-col>
    </v-row>
</template>

<script>
export default {
    name: 'Short',
    computed: {
        fifteenCrossFilter() { 
            const fifteen_thirty_list = {}
            for (const stock in this.$store.state.all_fifteen) {
                const fifteen_stock_data = this.$store.state.all_fifteen[stock]
                const thirty_stock_data = this.$store.state.all_thirty[stock]
                if (fifteen_stock_data.cloud_position === 'Below' && thirty_stock_data.cloud_position === 'Below') {
                    fifteen_thirty_list[stock] = { fifteen_stock_data, thirty_stock_data }
                } 
            }
            return fifteen_thirty_list
        },
    },
    methods: {
        goToStockLookup(stock){
            this.$router.push({ name: 'Stock', params: { id: stock } })
        }
    }
}
</script>

