<template>
    <v-row class="py-6">

        <v-sheet
            class="mx-auto"
            elevation="8"
            max-width="100%"
        >
            <h1>5 Cross Time Alerts</h1>
            <v-slide-group
                v-model="model"
                class="pa-4"
                show-arrows
                center-active
            >
                <v-slide-item
                    v-for="(alert, j) in fiveCrossFilter" :key="j"
                >
                    <v-card
                    class="ma-4"
                    >
                        <v-row
                            class="fill-height"
                            align="center"
                            justify="center"
                        >
                            <v-card class="pa-0" @click="goToStockLookup(j)">
                                <v-row justify="space-around">
                                    <v-card-title>{{ j }}</v-card-title>
                                    <v-card-title class="text-subtitle-1 text-center">5 Alert: {{ alert.five_stock_data.entry }}<br>5 Price: {{ alert.five_stock_data.price  | toCurrency()}}</v-card-title>
                                </v-row>
                                <v-card-text class="secondary white--text">
                                    <v-row justify="space-around" class="py-0">
                                        <v-card-subtitle class="text-center">15 RSI:</v-card-subtitle>
                                        <v-card-subtitle class="text-center">{{ alert.fifteen_stock_data.rsi }}</v-card-subtitle>
                                    </v-row>
                                    <v-row 
                                        justify="space-around" 
                                        class="py-0" 
                                        :class="{ 'yellow--text' : alert.fifteen_stock_data.k == 0 && alert.fifteen_stock_data.d == 0}"
                                    >
                                        <v-card-subtitle class="text-center">15 StochK:</v-card-subtitle>
                                        <v-card-subtitle class="text-center">{{ alert.fifteen_stock_data.k }}</v-card-subtitle>
                                        <v-card-subtitle class="text-center">15 StochD:</v-card-subtitle>
                                        <v-card-subtitle class="text-center">{{ alert.fifteen_stock_data.d  }}</v-card-subtitle>
                                    </v-row>
                                </v-card-text>
                            </v-card>
                        </v-row>
                    </v-card>
                </v-slide-item>
            </v-slide-group>
        </v-sheet>
    </v-row>
</template>

<script>
export default {
  name: 'aboveAlerts',
  data: () => ({
    model: null,
  }),
  components: {
  },
  mounted() {
  },
  methods: {
    goToStockLookup(stock){
      this.$router.push({ name: 'Stock', params: { id: stock } })
    },
    data_print() {
    },
  },
  filters: {
    toCurrency(amount) {
      var formatter = new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
      });
      return formatter.format(amount)
    },    
  },
  computed: {
    fiveCrossFilter() { 
        const five_fifteen_list = {}
        for (const stock in this.$store.state.all_five) {
            const five_stock_data = this.$store.state.all_five[stock]
            const fifteen_stock_data = this.$store.state.all_fifteen[stock]
            if ((five_stock_data.cloud_position === 'Above' && five_stock_data.entry === 'STRONG LONG ENTRY' && fifteen_stock_data.cloud_position === 'Above') || (five_stock_data.cloud_position === 'Below' && five_stock_data.entry === 'STRONG SHORT ENTRY' && fifteen_stock_data.cloud_position === 'Below')) {
                five_fifteen_list[stock] = { five_stock_data, fifteen_stock_data }
            } 
        }
        return five_fifteen_list
    }
  }
}
</script>

