<template>
    <v-row class="py-4">
        <v-col cols="6">
          <h2>15</h2>
          <div>Last Updated: <br> {{ lastUpdated }}</div>
        </v-col>

        <v-col cols="6">
            <h2>Stats</h2>
            <div>Above: {{ fifteenAbovePercentage }}</div>
            <div>Below: {{ fifteenBelowPercentage }}</div>
            <div>In: {{ fifteenInPercentage }}</div>
        </v-col>
    </v-row>
</template>

<script>
export default {
    name: 'Stats',
    data() {
        return {
        }
    },
    mounted() {
    },
    computed: {
        lastUpdated() {
            return this.$store.state.updateTimes.fifteen
        },
        fifteenAbovePercentage() {
            const above_list = []
            for (const stock in this.$store.state.all_fifteen) {
                const stock_data = this.$store.state.all_fifteen[stock]
                if (stock_data.cloud_position === 'Above') {
                    above_list.push(stock)
                }
            }
            return above_list.length
        },
        fifteenBelowPercentage() {
            const below_list = []
            for (const stock in this.$store.state.all_fifteen) {
                const stock_data = this.$store.state.all_fifteen[stock]
                if (stock_data.cloud_position === 'Below') {
                    below_list.push(stock)
                }
            }
            return below_list.length
        },
        fifteenInPercentage() {
            const in_list = []
            for (const stock in this.$store.state.all_fifteen) {
                const stock_data = this.$store.state.all_fifteen[stock]
                if (stock_data.cloud_position === 'In') {
                    in_list.push(stock)
                }
            }
            return in_list.length
        },
    }

}
</script>

